.main-app {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: $clr5;
    main {
        flex-grow: 1;
    }

    > footer {
        background-color: #13161f;
        color: $clr5;
        width: 100%;
        display: flex;
        align-items: center;
        text-align: center;
    }
}

input {
    padding: 2rem;
    font-size: 1.5rem;
    border-width: 1px;
    border-color: #CCCCCC;
    background-color: #FFFFFF;
    color: #000000;
    border-style: solid;
    border-radius: 0.8em;
    box-shadow: 0px 0px 5px rgba(66,66,66,.75);
}

input:focus {
    outline:none;
}